import axios from 'axios';


const getReadableDateFormat = (dateString) => {
    let dateJs = new Date(dateString);
    let result = `${dateJs.getDate()} ${dateJs.toLocaleString('default', { month: 'long' })} ${dateJs.getFullYear()} г. ${dateJs.getHours()}:${dateJs.getMinutes()}`
    return result;
}

const isUserAuth = () => {
    if (localStorage.getItem("jwt") != null) {
        return true;
    } else {
        return false;
    }
}

const getCurrentUserData = (jwt) => {
    let result = null

    axios.get("http://127.0.0.1:8000/api/v1/user/is_auth/", {
        headers: {
            Authorization: 'Bearer ' + jwt
        }
    })
        .then((response) => {
            result = response.data
        })
        .catch((error) => {
            console.log(error.response.status)
            result = false
        })

    return "You are pidor";
}

export { getReadableDateFormat, isUserAuth, getCurrentUserData };
